.navigation {
    //margin: 26px 0;
    display: grid;
    width: 264px;
    min-height: 900px;
    height: 100%;

    border-radius: 10px;
    
    position: relative;

    a {
        margin: 10px;
        padding: 10px;
        position: relative;
    }

    .active {
        &::after {
            content: "";
            width: 85%;
            height: 5px;
            background: $color-1;
            position: absolute;
            bottom: 1px;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}